@media all and (max-width: 650px){
	.bars{
		flex-direction: column;
		text-align: center;
	}
	.h_2 img{
		display: none;
	}
	.bar:hover{
	  	transform: scale(1.0);
	}
}


.h_1{
	background-color: #444444;
	display: flex;
	justify-content: flex-start;
}
.h_1 .block{
	margin: 5px;
	margin-left: 2%;
	color: #eeeeee;
}
.h_2{
	display: flex;
}
.h_2 img{
	width: 80px;
	height: 80px;
}
.h_2 .bars{
	background-color: #00b2dd;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.sign{
	display: flex;
	flex-direction: column;
}
.signin{
	border: 1px solid black;
	border-radius: 10px;
	overflow: auto;
}
.bar{
    padding: 10px;
    transition: ease-out .3s;
    text-decoration: none;
    color: black;
    
}
.bar:visited{
	color: black;
}
.bar:hover{
	background-color: #0080ff;

}
.bar:active{
	color:pink;
}
